<template>
    <div class="cart-box">
      <nav-bar title="购物车" :border="false" :leftArrow="false"></nav-bar>
      <div class="box" v-if="isLogin && cartList.length>0">
        <div class="top">
          <div class="left">共<span>{{ cartTotal }}</span>件商品</div>
          <div class="right" @click="editState=!editState">
            <van-icon name="edit" size="20" />
            <span>{{editState?'退出编辑':'编辑'}}</span>
          </div>
        </div>
        <div class="list" ref="listDom">
          <div class="item" v-for="item in cartList" :key="item.goods_id">
            <van-checkbox  checked-color="#ee0a24" :value="item.ischecked" @click="toggleCheck(item.goods_id)"></van-checkbox>
            <img :src="item.goods.goods_image" alt="" @click="$router.push(`/goods/${item.goods_id}`)">
            <div class="info">
              <div class="title line2" @click="$router.push(`/goods/${item.goods_id}`)">{{ item.goods.goods_name }}</div>
              <div class="bot">
                <div class="price"><span>￥</span>{{ item.goods.goods_price_min }}</div>
                <!-- 即希望保留原本的形参 又希望调用传参 =》 箭头函数包装一层 -->
                <count-box @input="(value) => changeCount(value, item)" :value="item.goods_num" :stock="item.goods.stock_total"></count-box>
              </div>
            </div>
          </div>
        </div>
        <van-submit-bar :price="selPriceTotal*100">
          <van-checkbox :value="isAllChecked" @click="toggleAllCheck">全选</van-checkbox>
          <template #button>
            <div :class="{ 'btn': true, 'disabled': selPayTotal ===0 }" v-if="!editState" @click="goPay">
              结算({{selPayTotal}})
            </div>
            <div :class="{ 'btn': true, 'disabled': selPayTotal ===0 }" v-else @click="delCart">
              删除({{selPayTotal}})
            </div>
          </template>
        </van-submit-bar>
      </div>
      <div class="empty-box" v-else>
        <van-empty  description="您的购物车是空的，快去逛逛吧">
          <van-button round  class="bottom-button" to="/home">去逛逛</van-button>
        </van-empty>
      </div>
    </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import CountBox from '@/components/CountBox.vue'
export default {
  name: 'CartIndex',
  components: {
    CountBox
  },
  data () {
    return {
      editState: false,
      scroll: 0 // 记录浏览位置
    }
  },
  created () {
    if (this.isLogin) {
      this.$store.dispatch('cart/getCartList')
    }
  },
  activated () {
    if (!this.$route.meta.canKeep && this.isLogin) {
      // 在这里发送请求，刷新数据
      this.$store.dispatch('cart/getCartList')
      return
    }
    this.$refs.listDom.scrollTop = this.scroll
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['cartTotal', 'selCartList', 'selPriceTotal', 'selPayTotal', 'isAllChecked']),
    isLogin () {
      return this.$store.getters.token
    }
  },
  watch: {
    editState (newVal, oldVal) {
      if (newVal) {
        this.$store.commit('cart/toggleAllCheck', false)
      } else {
        this.$store.commit('cart/toggleAllCheck', true)
      }
    }
  },
  methods: {
    toggleCheck (goodsId) {
      this.$store.commit('cart/toggleCheck', goodsId)
    },
    toggleAllCheck () {
      this.$store.commit('cart/toggleAllCheck', !this.isAllChecked)
    },
    changeCount (value, item) {
      const obj = {
        goodsId: item.goods_id,
        goodsNum: value,
        goodsSkuId: item.goods_sku_id
      }
      this.$store.dispatch('cart/upDateCart', obj)
    },
    async goPay () {
      if (this.selPayTotal === 0) return
      const cartIds = this.selCartList.map(item => item.id).join(',')
      this.$router.push({
        path: '/pay',
        query: { mode: 'cart', cartIds }
      })
    },
    async delCart () {
      if (this.selPayTotal === 0) return
      await this.$store.dispatch('cart/delCart')
      this.editState = false
    }
  },
  // 进入页面前执行的操作
  beforeRouteEnter (to, from, next) {
    if (from.name === 'GoodsDetail') {
      to.meta.canKeep = true
    } else {
      to.meta.canKeep = false
    }
    next()
  },
  // 离开页面执行操作
  beforeRouteLeave (to, from, next) {
    if (this.cartList.length > 0) {
      this.scroll = this.$refs.listDom.scrollTop || 0
    }
    next()
  }
}
</script>

<style lang="less" scoped>
.cart-box{
  .box{
    background-color: @primaryBg;
    padding: 41px 10px 50px;
    min-height: calc(100vh - 96px);
    .top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px;
      position: fixed;
      width: 100%;
      top: 46px;
      left: 0;
      z-index: 10;
      background-color: @primaryBg;
      .left{
        span{
          color: @primaryColor;
        }
      }
      .right{
        display: flex;
        align-items: center;
        span{
          margin-left: 5px;
        }
      }
    }
    .list{
      padding: 10px 0;
      height: calc(100vh - 187px);
      overflow-y: scroll;
      .item{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        background-color: #fff;
        border-radius: 10px;
        padding: 20px 10px;
        margin-bottom: 10px;
        img{
          width: 90px;
          height: 90px;
          margin: 0 10px;
        }
        .info{
          height: 90px;
          flex: 1;
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          justify-content: space-between;
          .title{
            font-size: 15px;
          }
          .bot{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .price{
              color: orangered;
              font-size: 18px;
              span{
                font-size: 14px;
              }
            }
          }
        }
      }
    }
  }
  .van-submit-bar{
    bottom: 50px;
    /deep/ .van-checkbox__icon--checked .van-icon{
      background-color: rgb(238, 10, 36);
      border-color: rgb(238, 10, 36);
    }
    .btn{
      width: 110px;
      height: 40px;
      line-height: 40px;
      background: linear-gradient(to right,#ff6034,#ee0a24);
      border-radius: 30px;
      color: #fff;
      text-align: center;
      &.disabled{
        opacity: .5;
      }
    }
  }
  .empty-box{
    height: calc(100vh - 96px);
    background-color: @primaryBg;
    .van-empty{
      height: 70%;
    }
    .bottom-button{
      color: #fff;
      background: linear-gradient(to right,#ee0a24,#ff6034);
      padding: 0 20px;
    }
  }
}
</style>
