import http from '@/utils/request'
export default {
  /* 获取图形验证码 */
  getCaptchaImage () {
    return http.get('/captcha/image')
  },
  /*
     登录-获取短信验证码
     默认的短信验证码为： 246810
  */
  getSmsCaptcha (captchaCode, captchaKey, mobile) {
    return http.post('/captcha/sendSmsCaptcha', {
      form: {
        captchaCode,
        captchaKey,
        mobile
      }
    })
  },
  /* 手机验证码登录 */
  login (mobile, smsCode) {
    return http.post('/passport/login', {
      form: {
        isParty: false,
        mobile,
        partyData: {},
        smsCode
      }
    })
  }
}
