import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'

// 首页相关默认加载
import Layout from '@/views/layout/index.vue'
import Home from '@/views/layout/home.vue'
import Category from '@/views/layout/category.vue'
import Cart from '@/views/layout/cart.vue'
import My from '@/views/layout/my.vue'

// 按需加载 异步组件
const Login = () => import('@/views/login/index.vue')
const Search = () => import('@/views/search/index.vue')
const SearchList = () => import('@/views/search/list.vue')
const GoodsDetail = () => import('@/views/goods/index.vue')
const Pay = () => import('@/views/pay/index.vue')
const Order = () => import('@/views/order/index.vue')
const editAddress = () => import('@/views/address/editAddress.vue')
const addressList = () => import('@/views/address/index.vue')
const OrderDetail = () => import('@/views/order/detail.vue')
const GoodsComment = () => import('@/views/goods/comment.vue')
const CouponList = () => import('@/views/coupon/index.vue')
const MyCoupon = () => import('@/views/my/coupon.vue')
const Help = () => import('@/views/my/help.vue')

Vue.use(VueRouter)

// 解决编程式路由往同一地址跳转时会报错的情况
const originalPush = VueRouter.prototype.push
const originalReplace = VueRouter.prototype.replace
// push
VueRouter.prototype.push = function push (location, onResolve, onReject) {
  if (onResolve || onReject) {
    return originalPush.call(this, location, onResolve, onReject)
  }
  return originalPush.call(this, location).catch(err => err)
}
// replace
VueRouter.prototype.replace = function push (location, onResolve, onReject) {
  if (onResolve || onReject) {
    return originalReplace.call(this, location, onResolve, onReject)
  }
  return originalReplace.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [
      {
        path: '/home',
        name: 'Home',
        component: Home,
        meta: {
          keepAlive: true, // 是否缓存
          canKeep: false // 是否保留数据
        }
      },
      {
        path: '/category',
        name: 'Category',
        component: Category
      },
      {
        path: '/cart',
        name: 'Cart',
        component: Cart,
        meta: {
          keepAlive: true,
          canKeep: false
        }
      },
      {
        path: '/my',
        name: 'My',
        component: My
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/search',
    name: 'Search',
    component: Search
  },
  {
    path: '/searchlist',
    name: 'SearchList',
    component: SearchList
  },
  {
    path: '/goods/:id',
    name: 'GoodsDetail',
    component: GoodsDetail,
    meta: {
      keepAlive: true,
      canKeep: false
    }
  },
  {
    path: '/pay',
    name: 'Pay',
    component: Pay
  },
  {
    path: '/editAddress',
    name: 'EditAddress',
    component: editAddress
  },
  {
    path: '/addressList',
    name: 'AddressList',
    component: addressList
  },
  {
    path: '/order',
    name: 'Order',
    component: Order,
    meta: {
      keepAlive: true,
      canKeep: false
    }
  },
  {
    path: '/order/:id',
    name: 'OrderDetail',
    component: OrderDetail
  },
  {
    path: '/comment/:id',
    name: 'GoodsComment',
    component: GoodsComment
  },
  {
    path: '/coupon',
    name: 'CouponList',
    component: CouponList
  },
  {
    path: '/myCoupon',
    name: 'MyCoupon',
    component: MyCoupon
  },
  {
    path: '/help',
    name: 'Help',
    component: Help
  }
]

const router = new VueRouter({
  routes
})

// 定义一个数组，专门存放需要权限访问的页面
const authUrls = ['/pay', '/order']

router.beforeEach((to, from, next) => {
  const token = store.getters.token
  if (authUrls.includes(to.path) && !token) {
    next('/login')
  } else {
    next()
  }
})

export default router
