<template>
    <div>
      <!-- 导航条 -->
      <van-nav-bar title="智慧商城" fixed z-index="100"  placeholder />

      <!-- 搜索 -->
      <van-search shape="round" background="#F1F1F1" placeholder="搜索你要找的商品" @click="$router.push(`/search?backUrl=${$route.fullPath}`)" />

      <!-- 轮播图 -->
      <van-swipe class="banner" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="item in bannerList" :key="item.imgName" @click="$router.push(`/goods/${item.link.param.query.goodsId}`)"><img :src="item.imgUrl" alt=""></van-swipe-item>
      </van-swipe>

      <!-- 公告 -->
      <van-notice-bar left-icon="volume-o" color="#ffffff" scrollable background="#525252" text="智慧商城2.0全新上线，更多新品等你来选~"/>

      <!-- 导航 -->
      <van-grid :column-num="5">
        <van-grid-item v-for="(item,index) in categoryList" :key="index" @click="$router.replace('/category')">
          <img :src="item.imgUrl" alt="">
          <div>{{ item.text }}</div>
        </van-grid-item>
      </van-grid>

      <!-- 主会场 -->
      <div class="main" @click="$router.replace('/category')">
        <img src="http://cba.itlike.com/public/uploads/10001/20230320/647c6bde4b1f1a3e88955e5f596344f9.png" class="img" alt="">
      </div>

      <!-- 猜你喜欢 -->
      <div class="guess">
        <p class="guess-title">———  猜你喜欢  ———</p>
        <div class="guess-list">
          <GoodsItem :goods="item" v-for="item in guessList.slice(0,10)" :key="item.goods_id"></GoodsItem>
        </div>
      </div>
    </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
export default {
  name: 'HomeIndex',
  components: {
    GoodsItem
  },
  data () {
    return {
      bannerList: [],
      categoryList: [],
      guessList: [],
      scroll: 0 // 记录浏览位置
    }
  },
  created () {
    this.getHomeData()
  },
  activated () {
    if (!this.$route.meta.canKeep) {
      // 在这里发送请求，刷新数据
      this.getHomeData()
      return
    }
    document.documentElement.scrollTop = this.scroll
  },
  methods: {
    async getHomeData () {
      const { data: { pageData: { items } } } = await this.$api.home.getHomeData()
      this.bannerList = items[1].data
      this.categoryList = items[4].data
      this.guessList = items[6].data
    }
  },
  // 进入页面前执行的操作
  beforeRouteEnter (to, from, next) {
    if (from.name === 'OrderDetail') {
      to.meta.canKeep = true
    } else {
      to.meta.canKeep = false
    }
    next()
  },
  // 离开页面执行操作
  beforeRouteLeave (to, from, next) {
    this.scroll = document.documentElement.scrollTop || 0
    next()
  }
}
</script>

<style lang="less" scoped>
/deep/ .van-nav-bar{
  background-color: @primaryColor;
  .van-nav-bar__title{
    color: #fff;
  }
}
.banner {
  width: 100%;
  .van-swipe-item {
    display: flex;
    img{
      width: 100%;
    }
  }
}
.van-grid-item__content{
  img{
    width: 45px;
  }
  div{
    font-size: 13px;
    color: #666;
    margin-top: 6px;
  }
}
.main{
  width: 100%;
  padding-top: 7px;
  background-color: #F2F2F2;
  display: flex;
  img{
    width: 100%;
  }
}
.guess{
  .guess-title{
    text-align: center;
  }
  .guess-list{
    background-color: #F2F2F2;
    padding: 1px 2px 0;
    .goods{
      margin-bottom: 5px;
    }
  }
}
</style>
