import api from '@/api/index.js'
import { Toast } from 'vant'
const user = {
  namespaced: true,
  state: {
    cartList: []
  },
  getters: {
    // 商品总数
    cartTotal (state) {
      return state.cartList.reduce((total, item) => total + item.goods_num, 0)
    },
    // 选中的商品列表
    selCartList (state) {
      const list = state.cartList.filter(item => item.ischecked)
      return list
    },
    // 选中的商品总价
    selPriceTotal (state, getters) {
      const total = getters.selCartList.reduce((total, item) => item.goods.goods_price_min * item.goods_num + total, 0)
      return total
    },
    // 选中的商品总数
    selPayTotal (state, getters) {
      const total = getters.selCartList.reduce((total, item) => item.goods_num + total, 0)
      return total
    },
    // 是否全选
    isAllChecked (state) {
      return state.cartList.every(item => item.ischecked)
    }
  },
  mutations: {
    setCartList (state, list) {
      state.cartList = list
    },
    // 选中商品
    toggleCheck (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.ischecked = !goods.ischecked
    },
    // 设置商品数量
    setNum (state, obj) {
      const { goodsId, goodsNum } = obj
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    },
    // 设置全选
    toggleAllCheck (state, isAllChecked) {
      state.cartList = state.cartList.map(item => {
        item.ischecked = isAllChecked
        return item
      })
    },
    // 删除购物车商品
    delCart (state, array) {
      array.forEach(item => {
        state.cartList = state.cartList.filter(ele => ele.id !== item)
      })
    }
  },
  actions: {
    async getCartList (context) {
      const { data: { list } } = await api.cart.getCartList()
      const cartList = list.map(item => {
        item.ischecked = true
        return item
      })
      context.commit('setCartList', cartList)
    },
    async upDateCart (context, obj) {
      const { goodsId, goodsNum, goodsSkuId } = obj
      // 先修改本地
      context.commit('setNum', { goodsId, goodsNum })
      // 再同步到后台
      await api.cart.upDateCart(goodsId, goodsNum, goodsSkuId)
    },
    async delCart (context) {
      const cartIds = context.getters.selCartList.map(item => item.id)
      context.commit('delCart', cartIds)
      await api.cart.delCart(cartIds)
      Toast('删除成功')
    }
  }
}

export default user
