<template>
    <div>
        <van-nav-bar :title="title" :left-arrow="leftArrow" fixed :border="border" placeholder z-index="100" @click-left="onClickLeft" />
    </div>
</template>

<script>
export default {
  props: {
    title: String,
    border: {
      type: Boolean,
      default: true
    },
    leftArrow: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    onClickLeft () {
      this.$router.back()
    }
  }
}
</script>

