import api from '@/api/index.js'
import router from '@/router'
import { Toast } from 'vant'
const address = {
  namespaced: true,
  state: {
    addressAll: [],
    defaultId: NaN // 默认收货地址id
  },
  getters: {
    defaultItem (state) {
      return state.addressAll.find(item => item.address_id === state.defaultId) || NaN
    },
    addressList (state) {
      return state.addressAll.filter(item => item.address_id !== state.defaultId).reverse() || []
    }
  },
  mutations: {
    setAddressAll (state, val) {
      state.addressAll = val
    },
    setDefaultId (state, val) {
      state.defaultId = val
    }
  },
  actions: {
    async getAddressList (context) {
      const { data: { list } } = await api.address.getAddressList()
      context.commit('setAddressAll', list)
      context.dispatch('getDefaultAddress')
    },
    async getDefaultAddress (context) {
      const { data: { defaultId } } = await api.address.getDefaultAddress()
      context.commit('setDefaultId', defaultId)
    },
    async setDefaultAddress (context, addressId) {
      await api.address.setDefaultAddress(addressId)
      Toast.success('修改成功')
      context.commit('setDefaultId', addressId)
    },
    async addAddress (context, form) {
      await api.address.addAddress(form)
      if (form.isdefault) {
        context.dispatch('getAddressList').then(result => {
          const defaultId = context.getters.addressList[0].address_id
          context.dispatch('setDefaultAddress', defaultId)
        })
      }
      router.replace('/addressList')
    },
    async editAddress (context, { addressId, form }) {
      await api.address.editAddress(addressId, form)
      if (form.isdefault) {
        context.dispatch('setDefaultAddress', addressId)
      }
      Toast.success('修改成功')
      const t = setTimeout(() => {
        router.replace('/addressList')
        clearTimeout(t)
      }, 1000)
    }
  }
}

export default address
