import http from '@/utils/request'
export default {
  getCouponList () {
    return http.get('/coupon/list')
  },
  getMyCoupon (dataType, page) {
    return http.get('/myCoupon/list', { params: { dataType, page } })
  },
  receiveCoupon (couponId) {
    return http.post('/myCoupon/receive', { couponId })
  }
}
