<template>
    <div class="container">
      <div class="info">
        <img :src="userInfo.avatar || defaultAvatar" alt="" class="avatar">
        <div class="text-box" v-if="isLogin">
           <div class="mobile">{{ userInfo.nick_name }}</div>
           <div class="label">
             <van-icon name="diamond" size="14" color="orange" />
             <span>普通会员</span>
           </div>
        </div>
        <div class="goLogin" v-else @click="$router.push(`/login?backUrl=${$route.fullPath}`)">点击登录</div>
      </div>
      <div class="tab-box">
        <div class="item">
          <div class="num">{{ assets.balance || 0 }}</div>
          <div class="text">账户余额</div>
        </div>
        <div class="item">
          <div class="num">{{ assets.points || 0 }}</div>
          <div class="text">积分</div>
        </div>
        <div class="item" @click="$router.push('/myCoupon')">
          <div class="num">{{ assets.coupon || 0 }}</div>
          <div class="text">优惠券</div>
        </div>
        <div class="item">
          <van-icon name="balance-pay" size="22" />
          <div class="text">我的钱包</div>
        </div>
      </div>
      <div class="card">
        <div class="tab-box">
          <div class="item" @click="$router.push('/order?dataType=all')">
            <van-icon name="balance-list-o" size="22" />
            <div class="text">全部订单</div>
          </div>
          <div class="item" @click="$router.push('/order?dataType=payment')">
            <van-icon name="clock-o" size="22" />
            <div class="text">待支付</div>
            <div class="top-num" v-if="counts.payment>0">{{ counts.payment }}</div>
          </div>
          <div class="item"  @click="$router.push('/order?dataType=delivery')">
            <van-icon name="logistics" size="22" />
            <div class="text">待发货</div>
            <div class="top-num" v-if="counts.delivery>0">{{ counts.delivery }}</div>
          </div>
          <div class="item"  @click="$router.push('/order?dataType=received')">
            <van-icon name="send-gift-o" size="22" />
            <div class="text">待收货</div>
            <div class="top-num" v-if="counts.received>0">{{ counts.received }}</div>
          </div>
        </div>
      </div>
      <div class="card serve">
        <div class="title">我的服务</div>
        <div class="tab-box">
          <div class="item" @click="$router.push('/addressList')">
            <van-icon name="records-o" size="22" color="rgb(216, 61, 5)" />
            <div class="text">收货地址</div>
          </div>
          <div class="item" @click="$router.push('/coupon')">
            <van-icon name="gift-o" size="22" color="rgb(216, 61, 5)" />
            <div class="text">领券中心</div>
          </div>
          <div class="item" @click="$router.push('/myCoupon')">
            <van-icon name="gift-card-o" size="22" color="rgb(216, 61, 5)" />
            <div class="text">优惠券</div>
          </div>
          <div class="item"  @click="$router.push('/help')">
            <van-icon name="question-o" size="22" color="rgb(216, 61, 5)" />
            <div class="text">我的帮助</div>
          </div>
          <div class="item">
            <van-icon name="balance-o" size="22" color="rgb(216, 61, 5)" />
            <div class="text">我的积分</div>
          </div>
          <div class="item">
            <van-icon name="refund-o" size="22" color="rgb(216, 61, 5)" />
            <div class="text">退换/售后</div>
          </div>
        </div>
      </div>
      <button class="logout" v-if="isLogin" @click="logout">退出登录</button>
    </div>
</template>

<script>
import defaultAvatar from '@/assets/images/user.jpg'
export default {
  name: 'MyIndex',
  data () {
    return {
      defaultAvatar,
      userInfo: {},
      assets: {},
      counts: {}
    }
  },
  created () {
    if (this.isLogin) {
      this.getUserInfo()
      this.getUserAssets()
      this.getUserTodoCounts()
    }
  },
  computed: {
    isLogin () {
      return this.$store.getters.token
    }
  },
  methods: {
    async getUserInfo () {
      const { data } = await this.$api.user.getUserInfo()
      this.userInfo = data.userInfo
    },
    async getUserAssets () {
      const { data } = await this.$api.user.getUserAssets()
      this.assets = data.assets
    },
    async getUserTodoCounts () {
      const { data } = await this.$api.user.getUserTodoCounts()
      this.counts = data.counts
    },
    logout () {
      // vuex 和 localStorage 数据要清除
      this.$dialog.confirm({
        message: '确定要退出登录吗？'
      })
        .then(() => {
          this.$store.dispatch('user/logout')
          localStorage.clear()
        })
        .catch(() => {})
    }
  }
}
</script>

<style lang="less" scoped>
.container{
  min-height: calc(100vh - 50px);
  background-color: @primaryBg;
  padding-bottom: 10px;
}
.info{
  background-color: #FBF3E8;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 40px 15px;
  .avatar{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    margin-right: 12px;
  }
  .goLogin{
    font-size: 17px;
  }
  .text-box{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    .mobile{
      font-size: 16px;
      color: #c98b28;
      font-weight: bold;
    }
    .label{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      background-color: #3D3B3B;
      border-radius: 5px;
      font-size: 13px;
      color: #f5e3d3;
      padding: 2px 5px;
      margin-top: 5px;
      .van-icon{
        margin-right: 3px;
        &::after{
          position: absolute;
          top: 0;
          left: 0;
          content: '\e6a5';
          color:#3D3B3B;
          transform: scale(.6);
        }
      }
    }
  }
}
.tab-box{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  width: 100%;
  .item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    padding: 20px 0;
    flex: 1;
    position: relative;
    .num{
      color: rgb(216, 61, 5);
    }
    .text{
      margin-top: 3px;
    }
    .top-num{
      width: 16px;
      height: 16px;
      line-height: 16px;
      border-radius: 50%;
      background-color: red;
      text-align: center;
      color: #fff;
      font-size: 12px;
      position: absolute;
      top: 15px;
      right: 25px;
    }
  }
}
.card{
  width: calc(100% - 16px);
  margin: 0 auto;
  padding-top: 8px;
  .tab-box{
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 8px;
  }
}
.serve{
    background-color: #fff;
    padding: 20px 0 10px;
    border-radius: 6px;
    .title{
      font-size: 15px;
      margin-left: 15px;
      margin-bottom: 10px;
    }
    .tab-box{
      flex-wrap: wrap;
      justify-content: flex-start;
      .item{
        flex: none;
        width: 25%;
        padding: 10px 0;
      }
    }
  }
.logout{
  display: block;
  width: 60%;
  font-size: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 8px;
  margin: 20px auto;
}
</style>
