import http from '@/utils/request'
export default {
  getCartTotal () {
    return http.get('/cart/total')
  },
  addCart (goodsId, goodsNum, goodsSkuId) {
    return http.post('/cart/add', { goodsId, goodsNum, goodsSkuId })
  },
  getCartList () {
    return http.get('/cart/list')
  },
  upDateCart (goodsId, goodsNum, goodsSkuId) {
    return http.post('/cart/update', { goodsId, goodsNum, goodsSkuId })
  },
  delCart (cartIds) {
    return http.post('/cart/clear', { cartIds })
  },
  payCart (mode, delivery, shopId, couponId, isUsePoints, cartIds) {
    return http.get('/checkout/order', { params: { mode, delivery, shopId, couponId, isUsePoints, cartIds } })
  }
}
