import { getInfo, setInfo } from '@/utils/storage'
const user = {
  namespaced: true,
  state: {
    userInfo: getInfo()
  },
  getters: {
  },
  mutations: {
    setUserInfo (state, val) {
      state.userInfo = val
      setInfo(val)
    }
  },
  actions: {
    logout (context) {
      context.commit('setUserInfo', {})
      // 跨模块访问mutations
      context.commit('cart/setCartList', [], { root: true })
      context.commit('address/setAddressAll', [], { root: true })
    }
  }
}

export default user
