<template>
    <div class="count-box">
        <button :class="{'sub':true, 'disabled':this.value <= 1}" @click="subClick">-</button>
        <input type="text" class="num" :value="value" @change="handleChange">
        <button :class="{'add':true, 'disabled':this.value >= this.stock}" @click="addClick">+</button>
    </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    },
    stock: {
      type: Number,
      default: 1
    }
  },
  methods: {
    subClick () {
      if (this.value > 1) {
        this.$emit('input', this.value - 1)
      } else {
        this.$toast('商品数量不能再减少了')
      }
    },
    addClick () {
      if (this.value < this.stock) {
        this.$emit('input', this.value + 1)
      }
    },
    handleChange (e) {
      const val = +e.target.value // 转数字处理 数字、NaN
      if (isNaN(val) || val < 1 || val > this.stock) {
        e.target.value = this.value
        return
      }
      this.$emit('input', val)
    }
  }
}
</script>

<style lang="less" scoped>
.count-box{
    display: flex;
    align-items: center;
    justify-content: center;
    button,input{
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        background-color: #F1F1EF;
        font-size: 18px;
    }
    .num{
        width: 40px;
        margin: 0 5px;
        font-size: 15px;
    }
    .disabled{
        opacity: .5;
    }
}
</style>
