import http from '@/utils/request'
export default {
  // 订单结算
  // mode: cart    => obj === {cartIds}
  //       buyNow  => obj === {goodsId,goodsNum,goodsSkuId}
  checkOrder (mode, obj) {
    return http.get('/checkout/order', {
      params: {
        mode, // 结算模式（buyNow立即购买 cart购物车）
        delivery: 10, // 配送方式（10快递配送 20上门自提）
        shopId: 0, // 自提门店ID
        couponId: 0, // 优惠券ID
        isUsePoints: 0, // 是否使用积分抵扣（1使用 0不使用）
        ...obj
      }
    })
  },
  // 提交订单
  // mode: cart    => obj === {cartIds}
  //       buyNow  => obj === {goodsId,goodsNum,goodsSkuId}
  submitOrder (mode, obj, remark, couponId) {
    return http.post('/checkout/submit', {
      delivery: 10,
      couponId,
      isUsePoints: 0,
      payType: 10,
      mode,
      ...obj,
      remark
    })
  },
  getOrderList (dataType, page) {
    return http.get('/order/list', {
      params: {
        dataType,
        page
      }
    })
  },
  getOrderDetail (orderId) {
    return http.get('/order/detail', {
      params: {
        orderId
      }
    })
  },
  cancelOrder (orderId) {
    return http.post('/order/cancel', { orderId })
  }
}
