<template>
    <div>
      <nav-bar title="分类列表" :border="false" :leftArrow="false"></nav-bar>
      <van-search v-model="searchVal" shape="round" background="#F2F2F2" placeholder="请输入搜索关键词" @click="$router.push(`/search?backUrl=${$route.fullPath}`)" />
      <div class="content">
        <van-sidebar v-model="categoryKey">
          <van-sidebar-item :title="item.name" v-for="item in categoryList" :key="item.category_id" />
        </van-sidebar>
        <van-grid :border="false" :column-num="3" v-if="categoryList[categoryKey] && categoryList[categoryKey].children">
          <van-grid-item  v-for="item in categoryList[categoryKey].children" :key="item.category_id" @click="$router.push(`/searchlist?categoryId=${item.category_id}`)">
            <van-image :src="item.image.external_url" />
            <div class="text">{{ item.name }}</div>
          </van-grid-item>
        </van-grid>
        <van-empty class="empty" image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
          description="暂无分类" v-else />
      </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue'
export default {
  components: { NavBar },
  name: 'CategoryIndex',
  data () {
    return {
      searchVal: '',
      categoryKey: 0,
      categoryList: []
    }
  },
  created () {
    this.getCategory()
  },
  methods: {
    async getCategory () {
      const { data } = await this.$api.category.getCategory()
      this.categoryList = data.list
    }
  }
}
</script>

<style lang="less" scoped>
.content{
  height: calc(100vh - 150px);
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  &>div{
    max-height: 100%;
    overflow-y: scroll;
  }
  .van-sidebar{
    width: 100px;
    text-align: center;
    .van-sidebar-item--select{
      color: @primaryColor;
       &::before{
          display: none;
        }
    }
  }
  .van-grid{
    flex: 1;
    padding: 10px 0;
    /deep/.van-grid-item__content--center{
      padding-top: 10px;
      padding-bottom: 10px;
    }
    .van-image{
      height: 76px
    }
    .text{
      font-size: 14px;
      margin-top: 8px;
    }
  }
  .empty{
    flex: 1;
    /deep/.van-empty__image{
      width: 100px;
      height: 100px;
    }
  }
}
</style>
