import http from '@/utils/request'
export default {
  getRegion () {
    return http.get('/region/tree')
  },
  addAddress (form) {
    return http.post('/address/add', { form })
  },
  getDefaultAddress () {
    return http.get('/address/defaultId')
  },
  setDefaultAddress (addressId) {
    return http.post('/address/setDefault', { addressId })
  },
  getAddressList () {
    return http.get('/address/list')
  },
  getAddressDetail (addressId) {
    return http.get('/address/detail', { params: { addressId } })
  },
  delAddress (addressId) {
    return http.post('/address/remove', { addressId })
  },
  editAddress (addressId, form) {
    return http.post('/address/edit', { addressId, form })
  }
}
