import http from '@/utils/request'
export default {
  getGoodsList (obj) {
    const { sortType, sortPrice, categoryId, goodsName, page } = obj
    return http.get('/goods/list', {
      params: {
        sortType: sortType,
        sortPrice: sortPrice,
        categoryId: categoryId,
        goodsName: goodsName,
        page: page
      }
    })
  },
  getGoodsDetail (goodsId) {
    return http.get('/goods/detail', { params: { goodsId } })
  },
  getGoodsComment (goodsId, limit) {
    return http.get('/comment/listRows', { params: { goodsId, limit } })
  },
  getGoodsService (goodsId) {
    return http.get('/goods.service/list', { params: { goodsId } })
  },
  getCommentTotal (goodsId) {
    return http.get('/comment/total', { params: { goodsId } })
  },
  getCommentList (scoreType, goodsId, page) {
    return http.get('/comment/list', { params: { scoreType, goodsId, page } })
  }
}
